*
{
  margin: 0;
  padding: 0;
}
.App-input
{
  text-align: center;
  background-color: rgb(186, 186, 236);
   height: 300px;
   color:rgb(49, 49, 109);
   font-size: 18px;
} 
input
{
font-size: 14px;
border-radius: 5px;
border: 1px solid grey;
outline: none;
padding: 10px 5px;
width: 200px;
}

.App-input button
{
  background-color: aqua;
  color: #e98d8d;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 10px;

}

.navActive
{
  color: rgb(176, 227, 133);
}

nav{
  background-color: rgb(63, 110, 118);
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
  color: rgb(222, 194, 194);
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgb(192, 160, 160);
  font-weight: bold;
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover
{
  transform:scale(1.4);
}
.App-container
{
  background-color: rgb(120, 123, 174);
  color: beige;
 padding: 10px;
 width: 100%;
}